export default {
	data () {
		return {
			userName: '',
			userPhone: '',
			customInfo: ''
		}
	},
	methods: {
		goBack () {
			uni.navigateBack({
				delta: 1
			})
		},
		async submitInfo () {
			if (this.userName.trim() == '' || this.customInfo.trim() == '') {
				uni.showToast({
					icon: 'none',
					title: '请检查输入的内容是否正确'
				})
				return
			}
			
			// 校验手机
			let reg = /^1[3456789]\d{9}$/
			if (!reg.test(this.userPhone)) {
				uni.showToast({
					icon: 'none',
					title: '请检查手机号输入是否正确'
				})
				return false
			}
			
			
		
			let res = await this.$fetch(this.$api.addCustomized,{name: this.userName,mobile: this.userPhone,content: this.customInfo})
				
			console.log(res)
			if (res.code) {
				return uni.showToast({
					icon: 'none',
					title: res.msg
				})
			} 
			uni.showToast({
				icon: 'success',
				title: res.msg
			})
			
			setTimeout(() => {
				uni.navigateBack({
					delta: 1
				})
			}, 1500)
			
		
		}
	}
}
